/* global window */
import React, { memo } from 'react';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { compose, withState } from 'recompose';
import Tracker from '../../utils/tracker';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import SettingsMenu from '../../components/SettingsMenu';
import styles from './styles';

const Slack = memo(({ classes, alert, setAlert }) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const onIntegrateSlack = () => {
    Tracker.hit('SlackIntegration - Opened');
    window.alert('Em breve lanaremos uma integrao com Slack!');
  };
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>Configuraes</b>
              </Typography>
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <SettingsMenu />
        </div>
        <br />
        <div className={classes.container}>
          <Paper className={classes.paper}>
            <section className={classes.slackBox}>
              <p className={classes.textCenter}>
                <img
                  src={require('../../assets/images/slack-integration.png')}
                  alt="Integrao Lobby com Slack"
                />
                <br />
                <br />
                <Typography variant="h6" component="strong">
                  <b>Transforme solicitaes no Slack em tickets</b>
                </Typography>
                <br />
                <Typography variant="body2" color="textSecondary">
                  <b>Leva menos de um minuto</b>
                </Typography>
              </p>
              <p className={classes.textCenter}>
                <Typography variant="subtitle2">
                  Colaboradores podero abrir novas solicitaes
                  <br />
                  {'e respostas via comando '}
                  <b>/lobby</b>
                  {' no Slack'}
                </Typography>
                <br />
                <Button onClick={onIntegrateSlack} size="large" color="primary" variant="contained">
                  Integrar Slack
                </Button>
              </p>
            </section>
          </Paper>
        </div>
      </section>
    </div>
  );
});

Slack.propTypes = {
  classes: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  })
)(Slack);
